.modal{
    background-color : transparent;
    box-shadow: none;
  }

  .modalheading{
    position: absolute;
    left: 85px;
    top:  85px;
  }

  .modalinputfrom{
    position: absolute;
    left: 150px;
    top: 125px;
    padding: 5px;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }

  .modalinputto{
    position: absolute;
    left: 150px;
    top: 170px;
    padding: 5px;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }

  .modalsubmitbtn{
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 8px 0;
    cursor: pointer;
    width: 20%;
    position: absolute;
    left: 150px;
    top: 210px;
  }

  .modalsubmitbtn:hover {
    opacity: 0.6;
  }

  .exportbutton{
    position: absolute;
    right: 10px;
    background-color: #848fc3;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 8px 0;
    top:100px;
    cursor: pointer;
}
.exportbutton:hover {
  opacity: 0.6;
}

.fromdate{
    top: 130px;
    left: 95px;
    position: absolute;
}
.todate{
    top: 174px;
    left: 95px;
    position: absolute;
}

.radiogroup{
  position: absolute;
    top: 22px;
    left: 160px;
    border: 1px solid;
    padding: 8px;
    background-color: #afd1f5;
}

.namedatalist{
  width: 40%;
    position: absolute;
    top: 130px;
    left: 155px;
    padding: 5px;
    border: none;
    background: #f0f1f3;
}

.downloading{
    opacity: 1 !important;
    position: absolute;
    animation: download_in_progress 1.5s;
    animation-iteration-count: infinite;
    right: 77px;
    top: 100px;
    z-index: 1;
}

@keyframes download_in_progress{
  from{top: 105px;}
  to{top:130px}
}

