.CashIn_Details_Scroll_Padding {
    padding: 0 2rem;
    transition: padding .3s cubic-bezier(.7,.3,.1,1);
}

@media only screen and (max-width: 960px){
.CashIn_Details_Scroll_Padding {
    padding: 0 1.4rem;
}
}

.CashIn_Details_Scroll{
    position: absolute;
    height: 70%;
    width: 40%;
    border: black;
}

*, :after, :before {
    box-sizing: inherit;
    min-width: 0;
}

section {
    display: block;
}


.CashIn_Details_Main{
    padding: 1rem 0;
    margin: 0;
}

.CashIn_Details_Title{
    margin: 0;
    padding: 0;
    font-size: 2rem;
    font-weight: 700;
    flex: 1 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.CashIn_Details_Wrapper{
    overflow: hidden;
    padding: .85rem 1rem;
    overflow-y: auto;
    background-color: #fff;
    height: 50%;
    position: absolute;
}

.CashIn_Details_Wrapper_Content {
    border: 1px solid;
    border-radius: 0.15rem;
    position: relative;
}

.GridHeader_header{
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    width: 100%;
    border-bottom: 1px solid;
    font-weight: 600;
    min-height: 2rem;
    padding: .65rem;
    background-color: rgba(0,0,0,.05);
    border-radius: .15rem .15rem 0 0;
}


.GridRecord{
    border-bottom: 1px solid;
}

.GridColumn_column{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: .2rem;
}
.GridColumn_value{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 1.25;
}

.GridRow_Row {
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    width: 100%;
    border-bottom: 1px solid var(--borderColor);
    min-height: 2rem;
    padding: .65rem;
    cursor: pointer;
    transition: background .2s;
    background-color: #aebfd4;
}

.GridRecord_View{
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    width: 100%;
    border-bottom: 1px solid var(--borderColor);
    min-height: 2rem;
    padding: .65rem;
    cursor: pointer;
    transition: background .2s;
}

.lazyloading{
    height: 400px;
    overflow: auto;
    width: 60%;
    position: absolute;
    top: 145px;
    left: 280px;
}